import React, { Component } from 'react';

import axios from 'axios';

import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';

import Typography from '@material-ui/core/Typography';

class TideTimes extends Component {

    state = {
        loading: false,
        siteId: 'salcombe',
    }

    componentDidMount() {
        this.loadData();
    }

    loadData = () => {
        this.setState({ loading: true });
        axios.get("https://opalescent-radiator.glitch.me?locationId=" + this.props.siteId, { crossdomain: true })
            .then(response => {
                console.log(response.data); //#body_id
                const json = response.data;
                this.setState({
                    loading: false,
                    siteId: this.props.siteId,
                    json
                });
            })
            .catch(error => {
                console.error("error: ", error);
                this.setState({
                    error: `${error}`,
                    loading: false
                });
            });
    }

    ucfirst(string) {
        return string.charAt(0).toUpperCase() + string.slice(1);
    }

    render() {

        console.log("TideTimes.render:...");
        console.log(this.state);

        const { loading, error, siteId, json } = this.state;

        if (loading) {
            return <Typography variant="h3">Loading...</Typography>
        }

        if (!json) {
            return <Typography variant="h3">No data yet</Typography>
        }
        if (error) {
            return (
                <Typography variant="h4">
                    There was an error loading the repos.{" "}
                    <button onClick={this.loadData}>Try again</button>
                </Typography>
            );
        }

        return (
            <div>
                <Typography variant="h4">{this.ucfirst(siteId)}</Typography>
                <Table>
                    <TableHead>
                        <TableRow>
                            <TableCell>Time</TableCell>
                            <TableCell>Height (meters)</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {/* <TableRow><TableCell /><TableCell /></TableRow> */}
                        {
                            (json[siteId]) ?
                                json[siteId].map((obj, i) => {
                                    return (
                                        obj ?
                                            <TableRow key={i}>
                                                <TableCell>{obj.time}</TableCell>
                                                <TableCell>{obj.height}</TableCell>
                                            </TableRow>
                                            : <TableRow key={i}></TableRow>
                                    );
                                })
                                : <p>Nuffink</p>
                        }
                    </TableBody>
                </Table>
            </div >
        );
    }
}

export default TideTimes;